'use strict';

var _d = require('d3');

var d3 = _interopRequireWildcard(_d);

function _interopRequireWildcard(obj) { if (obj && obj.__esModule) { return obj; } else { var newObj = {}; if (obj != null) { for (var key in obj) { if (Object.prototype.hasOwnProperty.call(obj, key)) newObj[key] = obj[key]; } } newObj.default = obj; return newObj; } }

var topojson = require('topojson');
var _ = require('lodash');
var $ = require('jquery');

var map = null;
var countryData = {};

var infoTemplate = _.template($("#infoTemplate").html());

var processCountryData = function processCountryData(data, keys) {
	console.log(data);
	var fills = {};
	keys.forEach(function (key) {
		fills[key.key] = key.colour;
	});
	data.forEach(function (o) {
		o.color = fills[o.fillKey];
		countryData[o.id] = o;
	});
	console.log(countryData);
	drawmap(countryData, fills);
	drawKey(keys);
};

var drawmap = function drawmap(countryData, fills) {
	var svg = d3.select("#map-container").append("svg");
	var width = $("svg").parent().width();
	var height = $("svg").parent().height();
	$("svg").attr("width", "100%");
	$("svg").attr("height", height + "px");
	console.log(width, height);
	var projection = d3.geoMercator().scale(width / 2 / Math.PI)
	// .scale(100)
	.translate([width / 2, height / 2]);
	var path = d3.geoPath().projection(projection);
	var url = "ne_110m_admin.json";
	var g = svg.append("g");

	d3.json(url, function (err, world) {
		var countries = topojson.feature(world, world.objects.ne_110m_admin_0_countries).features;
		g.selectAll(".country").data(countries).enter().insert("path", ".graticule").attr("class", "country").attr("d", path).attr("data-country", function (d) {
			return d.properties.iso_a3;
		}).attr("id", function (d) {
			return d.properties.iso_a3;
		}).style("fill", function (d, i) {
			if (countryData[d.properties.iso_a3]) {
				return countryData[d.properties.iso_a3].color;
			}
		}).on("click", function (d) {
			$("#info").html(infoTemplate(countryData[d.properties.iso_a3]));
			$("#info-container").removeClass("hide");
		});
		g.select("#ATA").remove();
	});
	var zoom = d3.zoom().scaleExtent([1, 5]).on("zoom", zoomed);
	svg.call(zoom);
	function zoomed() {
		g.style("stroke-width", 1 / d3.event.transform.k + "px");
		g.attr("transform", d3.event.transform);
	}
};

var drawKey = function drawKey(keys) {
	var template = _.template($('#legendKeyTemplate').html());
	keys.forEach(function (key) {
		$('#legend').append(template({ key: key }));
	});
};

var loadCSV = function loadCSV(csv) {
	return new Promise(function (resolve, reject) {
		d3.csv(csv, function (err, data) {
			if (err) return reject(err);
			resolve(data);
		});
	});
};

document.addEventListener('DOMContentLoaded', function () {
	// do your setup here
	console.log('Initialized app');
	var data = null;
	var keys = null;
	loadCSV("cannabis-data.csv").then(function (result) {
		data = result;
		return loadCSV("cannabis-keys.csv");
	}).then(function (result) {
		keys = result;
		processCountryData(data, keys);
	}).catch(function (err) {
		console.error(err);
	});
});

$(function () {
	$("#info-close").on("click", function (e) {
		$("#info-container").addClass("hide");
	});

	$("#sources").on("click", function (e) {
		e.preventDefault();
		var template = _.template($("#sourceTemplate").html());
		$("#info").html(template());
		$("#info-container").removeClass("hide");
	});
});
